.slider {
  font-family: "TT Lakes", sans-serif;
  background-color: #f8f8f8;
}
.slider__title {
  margin: 0;
  padding: 16px;
  line-height: 24px;
  font-size: 16px;
  text-transform: uppercase;
  color: #7e7e82;
  font-weight: 400;
  letter-spacing: -0.2px;
  background-image: linear-gradient(to right, #f8f8f8, #ffffff);
  position: relative;
}
.slider__title::before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: rgb(217, 255, 245);
  position: absolute;
  top: 0;
  left: 0;
}
@media (min-width: 768px) {
  .slider__title {
    padding: 16px 24px;
  }
}
@media (min-width: 1440px) {
  .slider__title {
    padding: 16px 32px;
  }
}
.slider__swiper {
  padding-left: 16px;
  padding-top: 16px;
  box-sizing: border-box;
  width: 100%;
}
.slider__swiper::before {
  content: "";
  width: 24px;
  height: 74px;
  position: absolute;
  right: 0px;
  top: 16px;
  background-image: linear-gradient(to left, rgb(248, 248, 248), rgba(248, 248, 248, 0));
  z-index: 100;
}
@media (min-width: 768px) {
  .slider__swiper {
    padding: 24px;
    padding-bottom: 0;
    overflow: hidden;
  }
}
@media (min-width: 1440px) {
  .slider__swiper {
    padding-left: 32px;
    padding-right: 32px;
  }
}
.slider__swiper-wrapper {
  -moz-column-gap: 16px;
       column-gap: 16px;
  height: 100px;
  padding-right: 64px;
}
@media (min-width: 768px) {
  .slider__swiper-wrapper {
    flex-wrap: wrap;
    height: 160px;
    row-gap: 16px;
    -moz-column-gap: 24px;
         column-gap: 24px;
    overflow: hidden;
  }
  .slider__swiper-wrapper--larger {
    height: auto;
  }
}
@media (min-width: 1440px) {
  .slider__swiper-wrapper {
    -moz-column-gap: 32px;
         column-gap: 32px;
  }
}
.slider__card {
  flex-basis: 206px;
  height: 70px;
  background-color: #ffffff;
  border: 1px solid #eaeaea;
  border-radius: 6px;
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .slider__card {
    flex-basis: 190px;
  }
}
@media (min-width: 1440px) {
  .slider__card {
    flex-basis: 206px;
  }
}
.slider__card-link {
  width: 36px;
  height: 36px;
  border: 2px solid #ff3e79;
  border-radius: 50%;
  background: url(./static/dropdown.svg) 50% 50% no-repeat;
}
.slider__swiper-button {
  display: none;
}
@media (min-width: 768px) {
  .slider__swiper-button {
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    margin-left: 33px;
    color: #1b1c21;
    position: relative;
    margin-top: 24px;
    display: block;
  }
  .slider__swiper-button::before {
    content: "";
    width: 24px;
    height: 24px;
    background-image: url(./static/expand.svg);
    left: -33px;
    position: absolute;
  }
}
.slider--larger {
  height: auto;
}

.button__icon--rotate::before {
  transform: scaleY(-1);
}

/* Пагинация */
