.modal-form {
  display: none;
  width: 100%;
  min-height: 100%;
  background-color: rgba(255, 255, 255, 0.95);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  font-family: "TT Lakes", sans-serif;
  overflow: scroll;
}
.modal-form__wrapper {
  box-sizing: border-box;
  width: 288px;
  min-height: 100%;
  z-index: 2000;
  position: absolute;
  top: 0;
  right: 0;
  padding: 24px 16px 70px 16px;
  background-color: #ffffff;
  box-shadow: -2px 0px 4px 0px rgba(69, 79, 126, 0.0196078431), 16px 0px 52px 0px rgba(14, 24, 80, 0.2);
}
@media (min-width: 1440px) {
  .modal-form__wrapper {
    width: 376px;
    padding: 40px 32px 70px 32px;
  }
  .modal-form__wrapper::before {
    content: "";
    width: 1px;
    height: 100%;
    background-color: rgb(217, 255, 245);
    position: absolute;
    top: 0;
    left: 0;
  }
}
.modal-form__content {
  position: relative;
}
.modal-form__button--close {
  background-image: url(./static/close.svg);
  width: 40px;
  height: 40px;
  margin-bottom: 40px;
}
@media (min-width: 1440px) {
  .modal-form__button--close {
    position: absolute;
    top: -8px;
    left: -105px;
  }
}
.modal-form__button--submit {
  width: 136px;
  height: 32px;
  background-color: #ff3e79;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 24px;
  letter-spacing: -0.15px;
  font-weight: 700;
  border-radius: 20px;
  text-align: left;
  padding-left: 20px;
  position: absolute;
  bottom: -50px;
  right: 0;
}
.modal-form__button--submit::before {
  content: "";
  width: 8px;
  height: 10px;
  background-image: url(./static/dropdown.svg);
  position: absolute;
  top: 11px;
  right: 20px;
}
.modal-form__title {
  line-height: 32px;
  position: relative;
  margin-bottom: 24px;
}
.modal-form__title::before {
  content: "";
  width: 4px;
  height: 32px;
  background-color: #41f6d7;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  position: absolute;
  left: -16px;
  bottom: 0;
}
@media (min-width: 1440px) {
  .modal-form__title::before {
    width: 3px;
    height: 40px;
    border-radius: 3px;
    left: -33px;
    bottom: 0;
  }
}
.modal-form__input {
  box-sizing: border-box;
  width: 100%;
  height: 48px;
  border: 1px solid #eaeaea;
  border-radius: 10px;
  font-family: "TT Lakes", sans-serif;
  font-size: 16px;
  line-height: 24px;
  padding-left: 16px;
  margin-top: 8px;
  margin-bottom: 8px;
}
.modal-form__input::-webkit-input-placeholder {
  color: #b5b6bc;
  letter-spacing: -0.2px;
}
.modal-form__input::-moz-placeholder {
  color: #b5b6bc;
  letter-spacing: -0.2px;
}
.modal-form__input:-ms-input-placeholder {
  color: #b5b6bc;
  letter-spacing: -0.2px;
}
.modal-form__input::placeholder {
  color: #b5b6bc;
  letter-spacing: -0.2px;
}
.modal-form__input--textarea {
  height: 116px;
  resize: none;
  padding-top: 12px;
}
.modal-form__text {
  font-size: 14px;
  line-height: 16px;
  color: #7e7e82;
  font-weight: 500;
}
